<template>
  <div class="userDetail_div">
    
    <div class="top_div">
      <p class="title"></p>
      <p class="name_p">{{name}}</p>
      <p class="idInfo_p">
        <span>: </span>
        <span>{{idNum}}</span>
      </p>
    </div>

    <div class="userDetail_cnt_div">
      <div class="userDetailList_div">
        <span class="necessary"></span>
        <van-cell-group>
          <van-field v-model="telVal" type="tel" clearable label="" maxlength="11" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p></p>
        <van-cell-group>
          <van-field v-model="addressVal" clearable label="" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div" @click="nationListClkFn">
        <p></p>
        <van-cell-group>
          <van-field v-model="nationVal" :class="{'active': nationVal != ''}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p></p>
        <van-cell-group>
          <van-field v-model="currentAddressVal" clearable label="" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div"  @click="maritalListClkFn">
        <p></p>
        <van-cell-group>
          <van-field v-model="maritalStatusVal" :class="{'active': maritalStatusVal != ''}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p>Email</p>
        <van-cell-group>
          <van-field v-model="emailVal" clearable label=""  placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div" @click="degreeListClkFn">
        <p></p>
        <van-cell-group>
          <van-field v-model="degreeVal" :class="{'active': degreeVal != ''}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p></p>
        <van-cell-group>
          <van-field v-model="graduateSchoolVal" clearable label=""  placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div" @click="graduateListClkFn">
        <p></p>
        <van-cell-group>
          <van-field v-model="graduateDateVal" :class="{'active': graduateDateVal != ''}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div" @click="registerStatusListClkFn">
        <p></p>
        <van-cell-group>
          <van-field v-model="registerStatusVal" :class="{'active': registerStatusVal != ''}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div" @click="politicsStatusListClk">
        <p></p>
        <van-cell-group>
          <van-field v-model="politicsStatusVal" :class="{'active': politicsStatusVal != ''}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <span class="necessary"></span>
        <van-cell-group>
          <van-field v-model="urgencyContactsVal" clearable label="" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <span class="necessary"></span>
        <van-cell-group>
          <van-field v-model="urgencyContactsTelVal" clearable label="" type="tel" maxlength="11" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <span class=" necessary"></span>
        <van-cell-group>
          <van-field v-model="urgencyContactsAddressVal" clearable label=""  placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <span class="necessary"></span>
        <van-cell-group>
          <van-field v-model="urgencyContactsRelationVal" clearable label=""  placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <span class="necessary"></span>
        <van-cell-group>
          <van-field v-model="bankNumVal" type="number" clearable label="" @blur="bankNumBlurFn"  placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <span class="necessary"></span>
        <van-cell-group>
          <van-field v-model="openBankVal" :class="{'active': openBankVal != ''}" disabled clearable label="" placeholder="" />          
        </van-cell-group>        
      </div>

      <div class="userDetailList_div">
        <p></p>
        <van-cell-group class="upload_div">
          <van-field v-model="idCardOnPathVal" clearable disabled label="" placeholder="" />
          
          <div class="uploadIcon" @click="idCardOnIconClkFn(1)">
            <van-button :icon="cameraIcon" plain></van-button>        
          </div>
        </van-cell-group>      
      </div>

      <div class="userDetailList_div">
        <p></p>
        <van-cell-group class="upload_div">
          <van-field v-model="idCardOffPathVal" clearable disabled label="" placeholder="" />

          <div class="uploadIcon" @click="idCardOnIconClkFn(2)">
            <van-button :icon="cameraIcon" plain></van-button>
          </div>
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p></p>
        <van-cell-group class="upload_div">
          <van-field v-model="academicCertificatePathVal" clearable disabled label="" placeholder="" />

          <van-uploader class="uploadIcon" :after-read="afterReadXl">
            <van-button :icon="cameraIcon" plain></van-button>
          </van-uploader>
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p></p>
        <van-cell-group class="upload_div">
          <van-field v-model="academicDegreePathVal" clearable disabled label="" placeholder="" />

          <van-uploader class="uploadIcon" :after-read="afterReadXw">
            <van-button :icon="cameraIcon" plain></van-button>
          </van-uploader>
        </van-cell-group>
      </div>

      <div class="userDetailList_div" @click="workDateListClkFn">
        <p></p>
        <van-cell-group>
          <van-field v-model="workDateVal" :class="{'active': workDateVal != ''}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p></p>
        <van-cell-group>
          <van-field v-model="workExperienceVal" type="textarea" autosize clearable label=""  placeholder="" />
        </van-cell-group>
      </div>

      <div class="footer_btn_div dsp_flex">
        <van-button plain class="ensure_btn btn" @click="ensureBtnClkFn"></van-button>
        <van-button plain class="cancel_btn btn" @click="cancelBtnClkFn"></van-button>
      </div>

    </div>
    
    <!-- picker -->
    <van-popup v-model="showPicker" round position="bottom" >
      <van-picker      
        title=""        
        show-toolbar 
        value-key="name"     
        :columns="columns"
        @confirm="onConfirm"
        @cancel="showPicker = false"            
      />
    </van-popup>

    <!--  -->
    <van-popup v-model="showDatePicker" round position="bottom" >
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        title=""
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="datePickerOnConfirm"
        @cancel="showDatePicker = false"
      />
    </van-popup>

    
  </div>
</template>

<script>
import './index.css'
import { formatTime, ToastFn, ToastLoadingFn } from "@/utils/common"
import { getBankCardNameApi, saveEmployeeUserApi, aliyunUploadFileApi, isTokenApi } from "@/utils/api"
import { 
  nationData, 
  maritalStatusData, 
  degreeData, 
  registerStatusData, 
  politicsStatusData,   
} from '@/utils/json.json'
import { Toast } from 'vant'

export default {
  data(){
    return{
      name:'',
      idNum: null,
      cameraIcon: 'http://static.life.youlanw.com/camera.png',
      telVal: null,
      addressVal: '',
      nationVal: '',
      currentAddressVal: '',
      maritalStatusVal: '',
      maritalStatusCode: null,
      emailVal: '',
      degreeVal: '',
      degreeCode: null,
      graduateSchoolVal:'',
      graduateDateVal: '',
      registerStatusVal:'',
      registerStatusCode: null,
      politicsStatusVal: '',
      politicsStatusCode: null,
      urgencyContactsVal: '',
      urgencyContactsTelVal: null,
      urgencyContactsAddressVal: '',
      urgencyContactsRelationVal: '',
      openBankVal: '',
      bankNumVal: null,
      idCardOnPathVal: '',
      idCardOffPathVal: '',
      academicCertificatePathVal:'',
      academicDegreePathVal: '',
      workDateVal:'',
      workExperienceVal: '',                        
      showPicker: false, 
      columns: [], 
      pickerSetId: null,
      showDatePicker: false,
      minDate: new Date(1970, 0, 1),
      maxDate: new Date(),    
      currentDate: new Date(),
      datePickerSetId: null,
      userInfoArr: [],      
    }
  },
  created(){        
    if(this.$route.query.userInfo){
      let _data = JSON.parse(this.$route.query.userInfo)

      this.idNum = _data.idNum
      this.name = _data.name
    }

    let $storage = window.sessionStorage
    let _storage = window.localStorage
    if($storage.getItem("idCardOnUrlPath")){
      this.idCardOnPathVal = $storage.getItem("idCardOnUrlPath")      
    }

    if($storage.getItem("idCardOffUrlPath")){
      this.idCardOffPathVal = $storage.getItem("idCardOffUrlPath")
    }    

    if(_storage.getItem('hroToken')){      
      this.getUserInfoIvkFn(_storage.getItem('hroToken'))
    }
  },

  computed: {
  
  },

  methods: {
    getUserInfoIvkFn(token){            
      let params = {
        token
      }      

      ToastLoadingFn('...', 0)
      isTokenApi(params).then(res => {
        ToastLoadingFn('...', 10)

        let {code, data, msg} = res

        if(code === 10000){
          this.userInfoArr = data
          
          let { 
            mobile, 
            permanentAddress, 
            nation, 
            currentAddress, 
            maritalStatus,
            email,
            education,
            graduateSchool,
            graduationTimeString,
            registeredResidenceType,
            politicalOutlook,
            emergencyContactName,
            emergencyContactMobile,
            emergencyContactAddress,
            emergencyContactRelationship,
            bankOfDeposit,
            bankAccount,
            cardFrontPath,
            cardBackPath,
            academicCertificatePath,
            academicDegreeCertificatePath,
            workingHoursTimeString,
            workExperience
            } = data

          this.telVal = mobile // 
          this.addressVal = permanentAddress // 
          this.nationVal = nation // 
          this.currentAddressVal = currentAddress // 
          this.maritalStatusCodeToName(maritalStatus) // 
          this.emailVal = email // 
          this.degreeCodeToName(education) // 
          this.graduateSchoolVal = graduateSchool, //           
          this.graduateDateValFormat(graduationTimeString) //                   
          this.registerStatusCodeToName(registeredResidenceType) //       
          this.politicsStatusCodeToName(politicalOutlook) // 
          this.urgencyContactsVal = emergencyContactName // 
          this.urgencyContactsTelVal = emergencyContactMobile // 
          this.urgencyContactsAddressVal = emergencyContactAddress  // 
          this.urgencyContactsRelationVal = emergencyContactRelationship  // 
          this.openBankVal = bankOfDeposit // 
          this.bankNumVal = bankAccount // 
          
          if(cardFrontPath){
            this.idCardOnPathVal = cardFrontPath // 
          }

          if(cardBackPath){
            this.idCardOffPathVal = cardBackPath // 
          }

          if(academicCertificatePath){
            this.academicCertificatePathVal = academicCertificatePath // 
          }
          
          if(academicDegreeCertificatePath){
            this.academicDegreePathVal = academicDegreeCertificatePath  // 
          }                  
          
          this.workDateValFormat(workingHoursTimeString)  // 

          this.workExperienceVal = workExperience // 
        }else{
          ToastFn(msg)
        }
      })
    },    
    
    maritalStatusCodeToName(code){      
      maritalStatusData.forEach(item => {        
        if(item.id == code){          
          this.maritalStatusVal = item.name          
        }
      })    
    },

    degreeCodeToName(code){
      degreeData.forEach(item => {        
        if(item.id == code){          
          this.degreeVal = item.name          
        }
      })  
    },

    registerStatusCodeToName(code){
      registerStatusData.forEach(item => {        
        if(item.id == code){          
          this.registerStatusVal = item.name          
        }
      })   
    },

    politicsStatusCodeToName(code){
      politicsStatusData.forEach(item => {        
        if(item.id == code){          
          this.politicsStatusVal = item.name          
        }
      })  
    },

    graduateDateValFormat(str){      
      if(!str){
        this.graduateDateVal = ''
      }else{
        if(str.indexOf(' ') > -1){
          this.graduateDateVal = str.substring(str.indexOf(' '), 0)
        }        
      }
    },

    workDateValFormat(str){
       if(!str){
        this.workDateVal = ''
      }else{
        if(str.indexOf(' ') > -1){
          this.workDateVal = str.substring(str.indexOf(' '), 0)
        }        
      }
    },

    nationListClkFn(){      
      this.showPicker = true
      this.columns = nationData
      this.pickerSetId = 1
    },
    
    maritalListClkFn(){      
      this.showPicker = true 
      this.columns = maritalStatusData
      this.pickerSetId = 2
    },

    degreeListClkFn(){
      this.showPicker = true 
      this.columns = degreeData
      this.pickerSetId = 3      
    },

    registerStatusListClkFn(){
      this.showPicker = true 
      this.columns = registerStatusData
      this.pickerSetId = 4     
    },

    politicsStatusListClk(){
      this.showPicker = true 
      this.columns = politicsStatusData
      this.pickerSetId = 5     
    },

    graduateListClkFn(){
      this.showDatePicker = true
      this.datePickerSetId = 1
    },
    
    workDateListClkFn(){
      this.showDatePicker = true
      this.datePickerSetId = 2
    },

    datePickerOnConfirm(e){
      this.showDatePicker = false      

      let _date = formatTime(e)

      let $id = this.datePickerSetId
      if($id === 1){
        this.graduateDateVal = _date
      }else if($id === 2){
        this.workDateVal = _date
      }
    },
    
    onConfirm(e){      
      this.showPicker = false
      
      let $id = this.pickerSetId
      if($id === 1){
        this.nationVal = e.name
      }else if($id === 2){
        this.maritalStatusVal = e.name
        this.maritalStatusCode = e.id
      }else if($id === 3){
        this.degreeVal = e.name
        this.degreeCode = e.id
      }else if($id === 4){
        this.registerStatusVal = e.name
        this.registerStatusCode = e.id
      }else if($id === 5){
        this.politicsStatusVal = e.name
        this.politicsStatusCode = e.id
      }

    },

    bankNumBlurFn(){
      let validateBankCardReg = /^([1-9]{1})(\d{14}|\d{18})$/;

      if(!this.bankNumVal){
        ToastFn('')
        this.openBankVal = ''
        return
      }else{
        if(!validateBankCardReg.test(this.bankNumVal)){
          ToastFn('')
          return
        }
      }
      

      let params = {
        idCard: this.bankNumVal
      }
      
      getBankCardNameApi(params).then(res => {
        let { code, msg, data } = res 
        
        if(data != ''){
          this.openBankVal = data          
        }else{
          ToastFn(data)          
        }
      })            
    },

    idCardOnIconClkFn(index){
      this.$router.push({
        path: '/uploadIdcard',
        query: {
          id: index,
          name: this.name
        }
      })
    },

    afterReadXl(file){
      this.afterRead(file, 1)
    },

    afterReadXw(file){
      this.afterRead(file, 2)
    },

    afterRead(file, id){      
      let formData = new FormData()
      formData.append('file', file.file)

      ToastLoadingFn('...', 0)
      aliyunUploadFileApi(formData).then(res => {
        ToastLoadingFn('...', 10)

        let { code, msg, data } = res
        if(code === 10000){
          Toast.success('')

          if(id == 1){  // 
            this.academicCertificatePathVal = data
          }else{ // 
            this.academicDegreePathVal = data
          }
        }else{
          ToastFn(msg)
        }
      })
        
      // OSS  header
      // this.$http.post('/api/perfectUser/alyUploadFile', formData,{
      //   headers: {
      //     'Content-Type': 'multipart/form-data'
      //   }
      // }).then(res => {
      //   console.log(res)
      // })
    },

    ensureBtnClkFn(){            
      let validateBankCardReg = /^([1-9]{1})(\d{14}|\d{18})$/;

      if(!this.telVal){
        ToastFn('')
        return
      }else{
        if (!/^1[3456789]\d{9}$/.test(this.telVal)) {
          ToastFn("")
          return
        }    
      }

      if(!this.emailVal){

      }else{
        if(!/^\w+@[a-z0-9]+\.[a-z]{2,4}$/.test(this.emailVal)){
          ToastFn("")
          return
        }
      }

      if(!this.urgencyContactsVal){
        ToastFn("")
        return
      }

      if(!this.urgencyContactsTelVal){
        ToastFn("")
        return
      }else{
        if (!/^1[3456789]\d{9}$/.test(this.urgencyContactsTelVal)) {
          ToastFn("")
          return
        }   
      }

      if(!this.urgencyContactsAddressVal){
        ToastFn("")
        return
      }

      if(!this.urgencyContactsRelationVal){
        ToastFn("")
        return
      }

      if(!this.bankNumVal){
        ToastFn('')        
        return
      }else{
        if(!validateBankCardReg.test(this.bankNumVal)){
          ToastFn('')
          return
        }
      }

      let params = {
        documentMobile: this.idNum, // Id 
        mobile: this.telVal,  // 
        permanentAddress: this.addressVal, // 
        nation: this.nationVal == '' ? null : this.nationVal, // 
        currentAddress:  this.currentAddressVal, // 
        maritalStatus: this.maritalStatusCode, // 
        email: this.emailVal, // 
        education: this.degreeCode, // 
        graduateSchool: this.graduateSchoolVal, // 
        graduationTimeStr:  this.graduateDateVal == '' ? null : this.graduateDateVal, // 
        registeredResidenceType: this.registerStatusCode, // 
        politicalOutlook: this.politicsStatusCode, // 
        emergencyContactName: this.urgencyContactsVal,  // 
        emergencyContactMobile: this.urgencyContactsTelVal, // 
        emergencyContactAddress: this.urgencyContactsAddressVal, // 
        emergencyContactRelationship: this.urgencyContactsRelationVal,  // 
        bankOfDeposit: this.openBankVal, // 
        bankAccount: this.bankNumVal, // 
        cardFrontPath: this.idCardOnPathVal,  // 
        cardBackPath: this.idCardOffPathVal, // 
        academicCertificatePath: this.academicCertificatePathVal, // 
        academicDegreeCertificatePath: this.academicDegreePathVal,  // 
        workingHoursTimeStr: this.workDateVal == '' ? null : this.workDateVal, // ' 
        workExperience: this.workExperienceVal // 
      }      

      saveEmployeeUserApi(params).then(res => {
        let { code, msg } = res

        if(code === 10000){
          Toast.success('')

          setTimeout(() => {
            this.$router.push({
              path: '/userPage'
            })
          },2000)
        }else{
          ToastFn(msg)
        }
      })
    },

    cancelBtnClkFn(){
      this.$router.push({
        path: '/userPage'
      })
    },
  }
}
</script>

<style scoped>

</style>


