import { requestGet, requestPost } from './http.js'
import Qs from 'qs'

// 
export function payslipLoginApi (params) {
  return requestPost(`api/Payslip/payslipLogin`, Qs.stringify(params)).then(res => res.data)  
}

// 
export function smsMobileApi (params){
  return requestGet(`api/Payslip/smsMobile`, params).then(res => res.data)
}

// 
export function isTokenApi (params){
  return requestGet(`api/Payslip/isToken`, params).then(res => res.data)
}

// list
export function searchEnterpriseListApi (params){
  return requestGet(`api/Payslip/searchEnterpriseList`, params).then(res => res.data)
}

// 
export function signOutApi (params){
  return requestGet(`api/Payslip/signOut`, params).then(res => res.data)
} 

// 
export function saveEmployeeUserApi (params) {
  return requestGet(`api/perfectUser/saveEmployeeUser`, params).then(res => res.data)  
}

// 
export function aliyunUploadFileApi (params) {
  return requestPost(`api/perfectUser/alyUploadFile`, params).then(res => res.data)  
}

// 
export function searchSalaryInfoListApi (params){
  return requestGet(`api/Payslip/searchSalaryInfoList`, params).then(res => res.data)
}

// 
export function getBankCardNameApi(params){
  return requestGet(`api/perfectUser/getBankCardName`, params).then(res => res.data)
}









