import { Toast } from "vant";

// Toast
export function ToastFn(msg) {
  return Toast({    
    message: msg,
    position: "bottom"
  })
}

// loading
export function ToastLoadingFn(msg, time){
  return Toast.loading({
    message: msg,
    forbidClick: true,
    duration: time
  })
}

//
export function IdentityCodeValid(code) {
  var city = { 11: "", 12: "", 13: "", 14: "", 15: "", 21: "", 22: "", 23: " ", 31: "", 32: "", 33: "", 34: "", 35: "", 36: "", 37: "", 41: "", 42: " ", 43: "", 44: "", 45: "", 46: "", 50: "", 51: "", 52: "", 53: "", 54: " ", 61: "", 62: "", 63: "", 64: "", 65: "", 71: "", 81: "", 82: "", 91: " " };
  var tip = "";
  var pass = true;

  var patt1 = new RegExp("(^[1-9]\\d{5}(18|19|([23]\\d))\\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\\d{3}[0-9Xx]$)|(^[1-9]\\d{5}\\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\\d{2}$)");
  if (!code || !patt1.test(code)) {
    tip = "";
    pass = false;
  } else if (!city[code.substr(0, 2)]) {
    tip = "";
    pass = false;
  } else {
    //18
    if (code.length == 18) {
      code = code.split('');
      //(aiWi)(mod 11)
      //
      var factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
      //
      var parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2];
      var sum = 0;
      var ai = 0;
      var wi = 0;
      for (var i = 0; i < 17; i++) {
        ai = code[i];
        wi = factor[i];
        sum += ai * wi;
      }
      var last = parity[sum % 11];
      if (parity[sum % 11] != code[17]) {
        tip = "";
        pass = false;
      }
    }
  }
  if (!pass)    
    Toast({
      message: tip,
      position: "bottom",
    })  
  return pass;
}

export function formatTime (date){
  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()
  const hour = date.getHours()
  const minute = date.getMinutes()
  const second = date.getSeconds()

  // return [year, month, day].map(formatNumber).join('/') + ' ' + [hour, minute, second].map(formatNumber).join(':')
  return [year, month, day].map(formatNumber).join('-')
}

export function formatNumber(n) {
  n = n.toString()  
  return n[1] ? n : '0' + n
}

export function replaceRegFn(str){
  return str.replace(/\//g, '-')
}



