import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/login/login' // 
import UserAgreement from '@/views/UserAgreement/index' // 
import UserPrivacy from '@/views/UserPrivacy/index' // 
import Index from '@/views/index/index' // 
import MyPage from '@/views/myPage/index' // 
import AboutUs from '@/views/aboutUs/index' // 
import UserDetail from '@/views/userDetail/index' // 
import UploadIdCard from '@/views/uploadIdCard/index' // 

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        keepAlive: true
      }
    },
    {
      path: '/userAgreement',
      name: 'userAgreement',
      component: UserAgreement
    },
    {
      path: '/UserPrivacy',
      name: 'UserPrivacy',
      component: UserPrivacy
    },
    {
      path: '/index',
      name: 'index',
      component: Index
    },
    {
      path: '/userPage',
      name: 'myPage',
      component: MyPage
    },
    {
      path: '/aboutUs',
      name: 'aboutUs',
      component: AboutUs
    },
    {
      path: '/userDetail',
      name: 'userDetail',
      component: UserDetail
    },
    {
      path: "/uploadIdcard",
      name: 'uploadIdcard',
      component: UploadIdCard
    },
    {
      path: '/',
      redirect: '/login'      
    }
  ]
})



